import React, { Component } from 'react';

import "./css/skills.css"
import Skill from "./Skill"

export default class Skills extends Component {
    constructor(props) {
        super(props)
        this.state = {
            js: "jsLogo.svg"
        }
    }

    render() {

        let listClass = "flex-horizontal space-between list-element"

        return (
            <div className="skillsDiv">
            <h1>Skills</h1>
                <li className={listClass} ><Skill logosrc={this.state.js} desc="Javascript" /> <Skill logosrc="angularLogo.svg" desc="Angular"/></li>
                <li className={listClass}><Skill logosrc="sql.svg" desc="SQL"/> <Skill logosrc="reactLogo.svg"  desc="React"/> </li>
                <li className={listClass}><Skill logosrc="cHashLogo.svg" desc="C# (.NET)"/> <Skill logosrc="unityLogo.svg" desc="Unity 3D (C#)" /></li>
                <center>Hover to see technologies' name</center><br/>
                
           
            </div>
        )
    }
}