import React, { Component } from 'react';
import "./css/projects.css"
import Project from './Project'
import NewProject from './NewProject';
import projectList from '../data/ProjectsData';



//Proper links
let projectArray = projectList;



export default class Projects extends Component {

    render() {
        return (
           
            <div className="projectsDiv pad">
            <h1>Projects</h1>
            <div className="cardContainer">
            {projectArray.map((proj,index) =>{
                const {title,description,img,projectLink,codeLink} = proj;

                return (
                    <Project key={index} title={title} desc={description} img={img} projectLink={projectLink} codeLink={codeLink}/>
                )
            })

            }
            </div>
            </div>
        )
    }
}