import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css'

const SIZE = 30;

const Skill = (props) =>{
    return (
        <div>
        <Tippy content={props.desc}>
        <img className="skillicon"  src={props.logosrc} width={SIZE} height={SIZE} alt="Fail"></img>
        </Tippy>
        
        </div>
        
    )
}

export default Skill